.container {
    display: flex; /* Usa flexbox para centrar el contenido */
    justify-content: center; /* Centra horizontalmente */
    height: 100%; /* Ocupa toda la altura de la p√°gina */
}
.TodoSearch {
    background-color: rgb(230, 214, 182);
    border-radius: 2px;
    border: 2px solid rgb(63, 40, 3);
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 200px);
    font-size: 24px;
    text-align: center;
    font-family: 'Single Day';
    font-weight: 400;
    color: rgb(101, 52, 8);
    box-shadow: 0px 5px 50px rgb(52, 26, 1);
}

.TodoSearch::placeholder{
    color: rgb(120, 104, 24);
    font-family: 'Single Day';
    font-weight: 400;
}

.TodoSearch:focus{
    outline-color: rgb(203, 181, 12);
}